
import loadMore from '@/components/loadmore'
import LoadMore from 'vux/src/components/load-more/index.vue'

import {companyEmploy} from '@/api/company.js'
export default {
components: {
loadMore,
LoadMore
},
created () {
this.userId = this.$route.query.userId
this.getrecruiment()
},
data () {
return {
historylists: [],
recruitmentId: '',
total: 0,
loading: false,
allLoaded: false,
bottomText: '',
endtext: ''
}
},
methods: {
loadBottom: function() {
if (!this.endtext) {
this.loading = true;
}
this.recruitmentId = this.historylists[this.historylists.length - 1].id
if (this.recruitmentId == 0) {
this.loading = false;
this.allLoaded = true;
return;
}
this.getrecruiment()
},
handleBottomChange(val) {
this.bottomText = val;
},
getrecruiment () {
let pdata = {
"userId": this.userId,
"pageLastId": this.recruitmentId
}

companyEmploy(pdata).then(res => {
if (res.data.result == 0) {
let lists = res.data.datas.list
if (lists.length == 0) {
this.endtext = '再也没有了...'
}
this.total = res.data.datas.total

if (this.recruitmentId == '') {

lists.forEach(item => {
this.historylists.push(item)
})


} else if (this.recruitmentId != 0) {
setTimeout(() => {
this.loading = false;
lists.forEach(item => {
this.historylists.push(item)
})
}, 600)
}

}


})
}
}
}



